.button {
  display: flex;
  text-align: center;
  justify-content: center;

  @include onDesktop {
    justify-content: left;
  }

  &__link {
    display: flex;
    text-align: center;
    justify-content: center;
    padding: 20px 0;
    width: 280px;

    font-size: 16px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 3px;
    color: $button-text-color;
    text-transform: uppercase;
    background-color: $menu-link-text-btn-bg-color;
    border-style: none;
    border: 1px solid $menu-link-text-btn-bg-color;

    &:hover {
      background-color: $main-bg-color;
      color: $menu-link-text-btn-bg-color;
      border: 1px solid $menu-link-text-btn-bg-color;
    }
  }

  &--shop-center {
    justify-content: center;
  }
}
